import request from '../request'
//按异常类型报警统计
export function logsByType(data) {
    return request({
        url: '/logs/byType',
        method: 'post',
        data
    })
}

//按设备号或者设备号报警统计
export function logsByObjectId(data) {
    return request({
        url: '/logs/byObjectId',
        method: 'post',
        data
    })
}

//按大队报警统计
export function logsByTeam(data) {
    return request({
        url: '/logs/byTeam',
        method: 'post',
        data
    })
}

